import React, { Component } from 'react';
import Audio from './Audio';
import Sentence from "./sentence";
import Words from "./word";
import logo from './logo.svg';
import './App.css';


//https://blog.csdn.net/csdt_csdt/article/details/11678105

const j = require("./data.json");



class App extends Component {
    state = {
        back:null,
        input:null,
        words:[],
        errors:[]
    }

    constructor(){
        super()
        this.control = this.control.bind(this)
        this.pass_article = this.pass_article.bind(this)
        this.load_article = this.load_article.bind(this)
        this.record_errors = this.record_errors.bind(this)
    }

    pass_article(e){
        // console.log(e.target.value)
        this.setState({input:e.target.value})
    }

    load_article(e){
        // console.log(this.state.input)
        this.setState({words:this.state.input.split(/\b/).filter(item=>item!==" ")})
    }

    record_errors(error_word){
        let tmp = this.state.errors
        if(tmp.indexOf(error_word)===-1){
            tmp.push(error_word)
            this.setState({errors:tmp})
        }
    }

    control(e){
        // console.log(e.keyCode)
        if(e.keyCode===13){
            // console.log("pause")
            this.setState({back:"pause"})
        }
        if(e.keyCode===93){
            // console.log("backward")
            this.setState({back:"backward"})
        }
        if(e.keyCode===18){
            // console.log('forward')
            this.setState({back:"forward"})
        }
    }

  render() {
    return(
        <div className="App">
            <div id="article">
                <textarea rows="4" cols="130" onChange={this.pass_article}/>
                <button onClick={this.load_article}>Load</button>
            </div>
            <div id="audio_div">
                <Audio back={this.state.back}/>
            </div>
            <div onKeyUp={this.control}>
                <Sentence words={this.state.words} record={(e)=>this.record_errors(e)}/>
            </div>
            <div className="summary">
                <h3>summary</h3>
                <Words words={this.state.errors}/>
            </div>

        </div>
  )}
}

export default App;
