import React, { Component } from 'react';
import { hot } from 'react-hot-loader'

class Sentence extends Component{

    constructor(){
        super();
        this.moveNext = this._moveNext.bind(this);
    }

    // componentDidMount(){
    //     // console.log(this.refs["1"].id);
    //     for (let x in this.refs){
    //         this.refs[x].onkeyup = (e) => this.moveNext(e,this.refs[x]);
    //     }
    //     // this.refs.what.focus();
    // }

    _moveNext(e) {

        e.preventDefault();
        // console.log(e.keyCode);
        let field = e.target;

        //检查对错
        if(field.className!=="unknown" && field.className!=="correct"){
            if(field.value!==field.name.toLowerCase()){
                field.className="wrong";
            }else{
                field.className="correct";
            }
        }

        if ((field.value.length === field.size)) {
            //输入框自动后移
            let next = this.refs[field.id].nextSibling;
            if(next){
                next.focus();
                while (next && !(next.tagName === "INPUT")) {
                    // console.log(next.tagName);
                    next = next.nextSibling;
                }
                if(next){
                    next.focus();
                    if(next.className===""||next.className==="wrong"){
                        next.value=null;
                    }
                }
            }


        }
        //输入框手动前移
        if (e.keyCode === 37) {
            // console.log(e.keyCode);
            e.preventDefault();
            let i = 1
            let last = this.refs[parseInt(field.id)-i];
            if(parseInt(field.id)-i>=0){
                while (typeof(last)=="undefined" && parseInt(field.id)-i>0) {
                    i += 1
                    last = this.refs[parseInt(field.id)-i];
                }

                if(last.className==="wrong"){
                    last.value = null;
                }
                last.focus();
            }
        }
        //输入框手动后移
        if (e.keyCode === 39) {
            e.preventDefault();
            let next = this.refs[field.id].nextSibling;
            if(next){
                next.focus();
                while (next && !(next.tagName === "INPUT")) {
                    next = next.nextSibling;
                }
                if(next){
                    next.focus();
                    if(next.className===""||next.className==="wrong"){
                        next.value=null;
                    }
                }
            }

        }

        if(e.keyCode===27){
            field.value = field.name
            field.className="unknown"
            this.props.record(field.name.toLowerCase())

        }
    }

    render(){
        return <div className="sentence">
            {this.props.words.map(
                (number,index) => {
                    if(number.search(/\n/)>-1){
                        return <span key={index} id={index} name={number}> {number} <br /> <br /></span>
                    }
                    else if(number.search(/\W/)===-1){
                        return <input type = "text" maxLength={number.length} key={index}
                                      size={number.length} name={number}
                                      ref={index} id={index} onKeyUp={this.moveNext}/>
                    }else{
                        return <span key={index} id={index}>{number}</span>
                    }

                })
            }
        </div>

    }
}

export default hot(module)(Sentence)